* {
  margin: 0;
  padding: 0;
  font-family: poppins, sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #7c8afc;
  justify-content: center;
  padding-top: 20px;
  display: flex;
}

main {
  color: #fff;
  background-color: #090c35;
  border-radius: 10px;
  flex-direction: column;
  max-width: 500px;
  max-height: fit-content;
  padding: 25px;
  display: flex;
}

.status {
  border: 3px solid #7c8afc;
  border-radius: 25px;
  padding: 25px;
}

.status__content {
  justify-content: space-around;
  display: flex;
}

.status__counter {
  background-color: #7c8afc;
  border: 30px solid #7c8afc;
  border-radius: 45px;
  font-size: 1.5rem;
}

.addTask {
  width: 100%;
  margin-top: 15px;
  margin-bottom: 100px;
  display: flex;
}

.addTask__input {
  border-radius: 5px;
  flex: 1;
  padding: 10px;
}

.addTask__button {
  color: #fff;
  cursor: pointer;
  background-color: #7c8afc;
  border-radius: 45px;
  width: 2.5rem;
  margin-left: .7rem;
}

.clearAll__button {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border-radius: 45px;
  width: 2.5rem;
  margin-left: .7rem;
}

.tasklist {
  flex-direction: column;
  gap: 10px;
  width: 100%;
  margin-top: 15px;
  display: flex;
}

.tasklist__task {
  align-items: center;
  gap: 15px;
  display: flex;
  position: relative;
}

.tasklist__all {
  background-color: #2e345c;
  border: 5px solid #000;
  border-radius: 15px;
  justify-content: center;
  align-items: start;
  padding: 10px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.tasklist__all p {
  word-wrap: break-word;
  max-width: 100%;
  margin-left: .7rem;
}

.tasklist__delete {
  color: red;
  cursor: pointer;
}

.tasklist__edit {
  color: #7c8afc;
  cursor: pointer;
  right: 5px;
}

.tasklist__checkbox {
  cursor: pointer;
}
